// <-- COMMON
$font-montserrat: 'Montserrat', sans-serif;
$font-caveat: 'Caveat', cursive;
$primary-color: #8E80A9;
$coral-color: #E65C7B;
$paddingForPositionOurTeam: 30px;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #FCF8FE;
}

@media (max-width: 768px), (min-width: 769px) {
  .section-kinds-of-yoga,
  .section-season-tickets,
  .section-our-team,
  .section-reviews {
    margin-bottom: 50px;
  }

  .section-kinds-of-yoga__h2,
  .section-season-tickets__h2,
  .section-our-team__h2,
  .section-reviews__h2 {
    margin-bottom: 35px;
  }
}

@media (max-width: 320px) {
  .section-kinds-of-yoga,
  .section-season-tickets,
  .section-our-team,
  .section-reviews,
  .section-kinds-of-yoga__h2,
  .section-season-tickets__h2,
  .section-our-team__h2,
  .section-reviews__h2 {
    margin-bottom: 20px;
  }
}

h2 {
  color: #8E80A9;
  font-family: $font-montserrat;
}

h3 {
  color: #8E80A9;
  font-weight: 500;
  font-family: $font-montserrat;
}

button {
  cursor: pointer;
}

// COMMON -->

// <-- HEADER

.header-page {
  background-repeat: no-repeat;
  background-color: #FCF8FE;
  background-size: cover;

  @media (max-width: 480px) {
    background-image: url(../images/header-bg@1x.png);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url(../images/header-bg@2x.png);
    }
  }

  @media (min-width: 481px) and (max-width: 868px) {
    background-image: url(../images/header-bg-768@1x.png);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url(../images/header-bg-768@2x.png);
    }
  }

  @media (min-width: 869px) {
    background-position: 0 90%;
    background-image: url(../images/header-bg-desktop@1x.png);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-position: 0 90%;
      background-image: url(../images/header-bg-desktop@2x.png);
    }
  }

}

.wrapper-supportive-header-page {
  display: flex;
  flex-direction: column;
  max-width: 1020px;
  margin: auto;

  @media (max-width: 480px) {
    height: 50vh;
  }

  @media (min-width: 481px) and (max-width: 868px) {
    height: 70vh;
  }

  @media (min-width: 869px) {
    height: 85vh;
  }

}

.header-page__navigation {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  font-family: $font-montserrat;
  position: fixed;
  z-index: 999;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.btn-nav-trigger {
  border: none;
  background: transparent;
}

.close {
  display: none;
}

.open {
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  padding: 20px;
  background: rgba(142, 128, 169, .7);
}

.line {
  width: 40px;
  height: 2px;
  background: #8E80A9;
  margin: 6px;
  cursor: pointer;
}


.list__list-item {
  margin: 10px;
  list-style-type: none;
}

.phone, .list__list-item > a {
  text-decoration: none;
}

.phone:hover,
.list__list-item > a:hover {
  text-decoration: underline;
}

.phone {
  color: #8E80A9;
  letter-spacing: 1px;
}

.list__list-item > a {
  color: #fff;
}

.header-page__main-content {
  transform: translate(0, -50%);
}

.main-headings__h2 {
  font-family: $font-caveat;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
  padding-bottom: 10px;

  @media (min-width: 868px) {
    font-size: 25px;
  }

  @media (min-width: 980px) {
    font-size: 35px;
  }
}

.main-headings__h1 {
  font-family: $font-montserrat;
  color: #fff;
  text-shadow: #c2c2c2 1px 0 10px;
  font-weight: 400;
  font-size: 15vw;
  text-align: center;
  letter-spacing: 16px;
  padding-bottom: 10px;
}

.btn-wrapper {
  font-family: $font-montserrat;
  display: flex;
  max-width: 200px;
  margin: auto;
}

.btn-wrapper__button {
  background: $coral-color;
  color: #fff;
  width: 30px;
  height: 26px;
  margin-right: 10px;
  border: none;
}

.btn-text {
  color: $coral-color;
  background: none;
  font-size: 10px;
}

.btn-text_link-anchor {
  text-decoration: none;
  color: $coral-color;

  &:hover {
    text-decoration: underline;
  }
}

// HEADER -->

// <-- SECTION KINDS OF YOGA

.section-kinds-of-yoga {
  font-family: $font-montserrat;
}

.wrapper-supportive-section-kinds-of-yoga {
  background-repeat: no-repeat;
  max-width: 1250px;
  margin: auto;

  @media (max-width: 785px) {
    background-size: 200px;
    background-position: 0 55%;
    background-image: url(../images/block-kinds-yoga-pattern-bg@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-size: 200px;
      background-position: 0 55%;
      background-image: url(../images/block-kinds-yoga-pattern-bg@2x.png);
    }
  }

  @media (min-width: 786px) {
    background-size: 800px;
    background-position: 40% 50%;
    background-image: url(../images/block-kinds-yoga-pattern-bg-786@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-size: 800px;
      background-position: 40% 50%;
      background-image: url(../images/block-kinds-yoga-pattern-bg-786@2x.png);
    }
  }

  @media (min-width: 980px) {
    background-size: 1190px;
    background-position: 100% 45%;
    background-image: url(../images/block-kinds-yoga-pattern-bg-desktop@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-size: 1080px;
      background-position: 40% 50%;
      background-image: url(../images/block-kinds-yoga-pattern-bg-desktop@2x.png);
    }
  }

  @media (min-width: 980px) and (max-width: 1240px) {
    background-position: 30% 45%;
    background-size: 1160px;
  }
}

.kind {
  margin-right: 15px;
  margin-left: 15px;
  margin-bottom: 30px;
}

.section-kinds-of-yoga__h2 {
  text-align: center;
}

.section-kinds-of-yoga__kinds {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.kinds__more-kinds {
  display: none;
  @media (min-width: 786px) {
      display: block;
      color: $primary-color;
      text-align: center;
      padding: 10px;
      font-size: 14px;
      background-color: #fff;
  }
}

@media (min-width: 786px) {
  .section-kinds-of-yoga__kinds {
    display: flex;
    flex-direction: column;
    align-content: center;
    max-height: 500px;
  }

  .section-kinds-of-yoga__kinds > * {
    margin: 15px;
  }
}

.kind__img {
  max-width: 100%;
  height: auto;
  display: block;
}

.kind-info {
  display: flex;
  flex-wrap: wrap;
  font-size: 10px;
  color: $primary-color;

  @media (min-width: 980px) {
    font-size: 13px;
  }

  & p:last-child {
    color: $coral-color;
  }

  &:last-child {
    justify-content: center;
  }
}

.kind__desc {
  flex-grow: 1;
  padding-right: 20px;
  padding-left: 20px;
  background-color: #fff;
  box-shadow: 0 0 5px 0 #e5e5e5;

  @media (max-width: 785px) {
    & > * {
      margin-bottom: 10px;
      margin-top: 10px;
    }
  }

}

.kind_big, .kind_small {
  transition: all .3s ease-out;
}

@media (min-width: 786px) {
  .kind_small {
    display: flex;
  }

  .kind_big .kind__desc > * {
    margin-top: 18px;
    margin-bottom: 18px;
  }
}

@media (min-width: 980px) {
  .kind_small .kind__desc > * {
    margin-top: 23px;
    margin-bottom: 23px;
  }
}

@media (min-width: 786px) and (max-width: 979px) {
  .kind_small .kind__desc > * {
    margin-top: 13px;
    margin-bottom: 13px;
  }
}

.btn-kind-details {
  background: #8E80A9;
  border: none;
}

@media (max-width: 639px) {
  .btn-kind-details {
    padding: 10px;
  }
}

@media (min-width: 640px) {
  .btn-kind-details__icon {
    margin: 8px;
    width: 10px;
  }
}


// <-- SECTION TICKETS

.wrapper-supportive-section-season-tickets {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @media (min-width: 980px) {
    padding: 100px 0;
    background-image: url(../images/block-tickets-sky-pattern-bg-desktop@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      padding: 100px 0;
      background-image: url(../images/block-tickets-sky-pattern-bg-desktop@2x.png);
    }
  }

  @media (min-width: 481px) and (max-width: 979px) {
    padding: 30px 0;
    background-image: url(../images/block-tickets-sky-pattern-bg-768@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      padding: 30px 0;
      background-image: url(../images/block-tickets-sky-pattern-bg-768@2x.png);
    }
  }

  @media (max-width: 480px) {
    background-image: url(../images/block-tickets-sky-pattern-bg@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url(../images/block-tickets-sky-pattern-bg@2x.png);
    }
  }

}

.section-season-tickets__tickets {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tickets__ticket {
  text-align: center;
  background-color: #fff;
  box-shadow: 0 0 5px 0 #e5e5e5;
  margin: 0 20px 40px 20px;
  padding: 20px 30px;
  flex-basis: 250px;
  transition: transform .2s ease-in-out;

  @media (min-width: 868px) {
    margin-bottom: 0;
  }
  @media (max-width: 480px) {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.tickets__ticket:hover {
  transform: scale(1.1);
}

.section-season-tickets__h2 {
  text-align: center;
}

.ticket-desc > * {
  font-family: $font-montserrat;
  margin-bottom: 15px;
}

.ticket-desc {
  font-family: $font-montserrat;
}

.ticket-desc > h3 {
  font-size: 20px;
  font-weight: 700;
}

.ticket-time {
  color: $coral-color;
  font-size: 12px;
}

.ticket-price {
  color: $primary-color;
  font-size: 18px;
  font-weight: 700;
}

.btn-ticket {
  padding: 20px 30px;
  background: $primary-color;
  border: none;
  color: #fff;
  font-family: $font-montserrat;
  font-size: 14px;

  &:hover {
    background: darken($primary-color, 10%);
  }
}


// SECTION TICKETS -->

// <-- SECTION OUR TEAM

.section-our-team {
  font-family: $font-montserrat;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;

  @media (min-width: 769px) {
    padding: 0 20px;
  }

  & .flickity-viewport {
    margin-bottom: 50px;
  }

  @media (min-width: 981px) {
    background-image: url(../images/block-our-team-pattern-bg-desktop@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url(../images/block-our-team-pattern-bg-desktop@2x.png);
    }
  }

  @media (max-width: 980px) {
    background-position: left top;
    background-image: url(../images/block-our-team-pattern-bg-768@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-position: left top;
      background-image: url(../images/block-our-team-pattern-bg-768@2x.png);
    }
  }

  @media (max-width: 480px) {
    background-position: left top;
    background-image: url(../images/block-our-team-pattern-bg@1x.png);
    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-position: left top;
      background-image: url(../images/block-our-team-pattern-bg@2x.png);
    }
  }

}

.wrapper-supportive-section-our-team {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px 0;

  @media (max-width: 768px) {
    max-width: 400px;
  }

  @media (min-width: 980px) {
    padding-bottom: 100px;
  }
}

.section-our-team__h2 {
  text-align: center;
}

.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  box-shadow: 0 0 3px 0 #e5e5e5;
  margin: 5px 10px 20px 10px;
  font-family: $font-montserrat;
  cursor: pointer;

  & > p {
    font-size: 10px;
    color: $coral-color;
    font-weight: 500;
    line-height: 2;
    @media (max-width: 364px) {
      margin-right: 10px;
      line-height: 1.5;
    }
  }
}

@media (max-width: 767px) {
  .icons {
    margin-right: 10px;
  }
}

@media (min-width: 768px) {
  .icons {
    display: flex;
  }
}

.social-links__icon {
  display: block;
  width: 15px;

  @media (min-width: 769px) {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  @media (min-width: 480px) {
    width: 18px;
  }

  @media (max-width: 768px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  border-bottom: 1px solid transparent;
  transition: all .3s ease-in-out;
}

.social-links__btn {
  color: $primary-color;
  border: none;
  border-bottom: 2px solid;
  padding-bottom: 2px;
  background: transparent;
  transition: transform .3s ease-in-out;

  &:hover {
    transform: scale(1.2);
  }

  @media (max-width: 767px) {
    display: none;
  }
}

.experience {
  font-family: $font-montserrat;
  font-size: 12px;
  margin-left: 10px;
  @media (max-width: 868px) {
    font-size: 11px;
  }
}

.experience__heading {
  color: $coral-color;
  margin-bottom: 15px;
}

.experience__text {
  color: $primary-color;
  line-height: 1.5;
}

// <-- service styles for flickity's carousel

.flickity-page-dots {
  display: none;
}

.flickity-button-icon {
  fill: white;
}

.flickity-prev-next-button:hover {
  background: hsla(261, 24%, 55%, 0.6)
}

.section-our-team__carousel-team .flickity-prev-next-button {
  width: 15px;
  height: 34px;
  top: 30px;
}

.flickity-prev-next-button {
  border-radius: 0;
  background-color: #8E80A9;
}

.section-our-team .previous {
  left: -20px;
}

.section-our-team .next {
  right: -20px;
}

.carousel-cell {
  margin-right: 20px;
  width: 100%;
  transition: opacity .5s ease-in-out, width .5s ease-in-out;

  &:not(.is-selected) {
    opacity: 0;
  }
}

// service styles for flickity's carousel -->

.carousel__carousel-status {
  position: relative;
  z-index: -1;
  top: -15px;
  margin: 0 20px;
  text-align: center;
  font-size: 14px;
  letter-spacing: 4px;
}

.section-our-team input[type="radio"] {
  display: none;
}

@media (max-width: 480px) {
  .section-our-team .photo {
    width: 60px;
    margin-right: 20px;
  }
}

@media (max-width: 768px) {
  .section-our-team .carousel__carousel-status {
    display: none;
  }
}

@media (min-width: 769px) {

  .cell {
    position: static !important;
    left: 0 !important;
    display: flex;
    padding-top: $paddingForPositionOurTeam;
  }

  @media (min-width: 640px) {
    .section-our-team .previous {
      left: 40%;
      top: 95%;
    }

    .section-our-team .next {
      right: 40%;
      top: 95%;
    }
  }

  // service styles for flickity's carousel -->

  .info {
    padding-right: 20px;
    min-width: 300px;
    height: 60px;

    & > p {
      font-size: 10px;
      line-height: 1.5;
    }
  }

  .section-reviews .photo {
    width: 45px;
  }

  .info__social-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: stretch;
    justify-content: space-around;
  }

  .social-links__icon {
    width: 15px;
  }

  .photo_pos-left {
    position: relative;
    left: -10px;
    bottom: 20px;
  }

  .experience {
    //display: none;
    opacity: 0;
    transition: opacity .5s ease-out;
  }

  .experience__wrapper {
    position: absolute;
    margin-left: 10px;
    top: 0;
    padding-top: $paddingForPositionOurTeam;
  }

  .cell :checked ~ .experience {
    //display: block;
    opacity: 1;
  }
}

// SECTION OUR TEAM -->

// <-- REVIEWS

.section-reviews {
  font-family: $font-montserrat;

  & .photo {
    display: block;
    margin-right: 15px;
    width: 41px;
  }
}

.section-reviews__h2 {
  text-align: center;
}

.section-reviews .previous,
.section-reviews .next {
  top: 95%;
  width: 25px;
  height: 20px;
}

.section-reviews .previous {
  @media (max-width: 768px) {
    left: calc(40% - 25px);
  }

  @media (min-width: 769px) {
    left: calc(45% - 15px);
  }
}

.section-reviews .next {
  @media (max-width: 768px){
    right: calc(40% - 25px);
  }

  @media (min-width: 769px) {
    right: calc(45% - 15px);
  }
}

.section-reviews .carousel__carousel-status {
  top: -5px;
}

.review {
  width: 90%;
  // 4px === size box-shadow
  margin-top: 4px;
  margin-bottom: 30px;
  margin-right: 15px;
  background-color: #fff;
  box-shadow: 0 0 4px #e5e5e5;
  font-size: 12px;
  transition: opacity .2s ease-in-out;

  & > * {
    margin: 15px;
  }

  &:not(.is-selected) {
    opacity: 0;
  }

  @media (min-width: 640px) {
    width: 43%;
  }

  @media (min-width: 980px) {
    width: 30%;
  }

  @media (min-width: 1280px) {
    width: 22.5%;
  }

  @media (min-width: 1440px) {
    width: 18%;
  }
}

.review__wrapper-icon {
  margin-bottom: 15px;
}

.review__text {
  color: $primary-color;
  line-height: 1.5;
  margin-bottom: 25px;
}

.review__user {
  display: flex;
}

.review__photo {
  margin-right: 15px;
}

.review__user-name {
  color: $coral-color;
  margin-bottom: 5px;
  font-weight: 500;
}

.review__date {
  color: $primary-color;
}

// REVIEWS -->


// <-- APPLICATION FORM

.section-application-form {
  background-repeat: no-repeat;
  background-size: cover;


  @media (max-width: 767px) {
    padding: 60px 40px;
    background-image: url(../images/footer-block-form-bg-768@2x.jpg);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      padding: 60px 40px;
      background-image: url(../images/footer-block-form-bg-768@1x.jpg);
    }
  }

  @media (max-width: 360px) {
    background-image: url(../images/footer-block-form-bg@2x.jpg);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url(../images/footer-block-form-bg@1x.jpg);
    }
  }

  @media (min-width: 768px) {
    padding: 100px 40px;
    background-image: url(../images/footer-block-form-bg-desktop@2x.jpg);

    @media (min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      padding: 100px 40px;
      background-image: url(../images/footer-block-form-bg-desktop@1x.jpg);
    }
  }
}

.wrapper-section-application-form {
  display: flex;
  max-width: 1020px;
  justify-content: flex-end;
  margin: 0 auto;
}

@media (max-width: 895px) {
  .wrapper-section-application-form {
    justify-content: center;
  }
}

.application-form__form {
  display: flex;
  flex-direction: column;
  max-width: 450px;
}

.application-form__form > *:not(:last-child) {
  margin-bottom: 30px;
}

.application-form__form input::placeholder {
  color: #C8C0DB;
}

.form__input-user-name,
.form__input-user-phone-number {
  border-style: none;
  border-bottom: 1px solid $primary-color;
  font-size: 14px;
  padding-bottom: 10px;
  background-color: transparent;
}

.form__submit-button {
  padding: 20px;
  background-color: $coral-color;
  color: #fff;
  font-size: 14px;
  border: none;
}

.only-screenreader {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

// APPLICATION FORM -->

// <-- FOOTER

.wrapper-footer {
  max-width: 1020px;
  margin: 0 auto;
}

.footer__section-info {
  background-color: $primary-color;
  padding: 30px 40px;
  font-family: $font-montserrat;
}

.footer__contacts {
  display: flex;
  flex-wrap: wrap;
}

.contacts {
  color: #fff;
}

@media (min-width: 896px) {
  .footer {
    position: relative;
  }

  .footer__contacts {
    position: absolute;
    top: 30%;
  }

  .contacts {
    color: $primary-color;
  }

  .footer__about-site {
    justify-content: space-between;
  }

  .about-site__right {
    order: 1;
  }

  .social-links {
    position: absolute;
    top: 55%;
  }

  .icon path {
    fill: $primary-color;
    fill-opacity: 0.65;
  }

}

.heading {
  font-size: 18px;
}

.contacts p:last-child,
.footer__about-site {
  font-size: 12px;
}

.social-links {
  margin-bottom: 20px;
  margin-right: 20px;
}

.contacts > .contacts:first-child,
.contacts > .contacts:last-child {
  margin-bottom: 20px;
}

.contacts {
  margin-right: 20px;
}

.icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.footer__about-site {
  display: flex;
  color: #ddd;
}

@media (max-width: 480px) {
  .footer__about-site {
    flex-direction: column;
  }
}

@media (max-width: 896px) {
  .about-site__right {
    margin-right: 20px;
  }
}

